.cover-container {
    height: 100vh;
    width: 100%;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgb(6, 126, 173);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .cover-img {
    width: 45%;
    display: flex;
    align-self: center;
    overflow: hidden;
  }
  
  .cover-container > h1 {
    color: rgb(0, 0, 0);
    font-size: 100px;
  }
  
  .cover-container > p {
    margin-top: 8px;
    color: rgb(0, 0, 0);
    font-size: 32px;
  }
  
  @media screen and (max-width: 960px) {
    .cover-container > h1 {
      font-size: 70px;
      text-align: center;
    }
  
    .cover-container > p {
      text-align: center;
      font-size: 22px;
      width: 85%;
    }
  }