.footer-container {
    height: 30vh;
    width: 100%;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgb(0, 92, 153);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .video {
    object-fit: footer;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -1;
  }
  
  .footer-container > h1 {
    color: rgb(0, 0, 0);
    font-size: 100px;
  }
  
  .footer-container > p {
    margin-top: 8px;
    color: rgb(0, 0, 0);
    font-size: 32px;
  }
  
  @media screen and (max-width: 960px) {
    .footer-container > h1 {
      font-size: 70px;
      text-align: center;
    }
  
    .footer-container > p {
      text-align: center;
      font-size: 22px;
      width: 85%;
    }
  }